div.tfw-factory-dialog {
    z-index: 999999;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, .2);
    padding: 0;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;
    padding: 1rem;
}

div.tfw-factory-dialog.align- {
    justify-content: center;
    align-items: center;
}

div.tfw-factory-dialog.align-TL {
    justify-content: flex-start;
    align-items: flex-start;
}

div.tfw-factory-dialog.align-TR {
    justify-content: flex-end;
    align-items: flex-start;
}

div.tfw-factory-dialog.align-BL {
    justify-content: flex-start;
    align-items: flex-end;
}

div.tfw-factory-dialog.align-BR {
    justify-content: flex-end;
    align-items: flex-end;
}

div.tfw-factory-dialog.background-dark {
    background: rgba(0, 0, 0, .2);
}

div.tfw-factory-dialog.background-light {
    background: rgba(255, 255, 255, .2);
}

div.tfw-factory-dialog.background-none {
    background: transparent;
}

div.tfw-factory-dialog>div {
    transition: all .2s;
    transform: scale(0);
    opacity: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: stretch;
    max-height: calc(100vh - 16px);
    max-width: calc(100vw - 16px);
}

div.tfw-factory-dialog.show>div {
    transition: all .2s;
    transform: scale(1);
    opacity: 1;
}

div.tfw-factory-dialog.show>div>div {
    padding: .5rem;
    flex: 1 1 auto;
    overflow: auto;
    overflow-wrap: anywhere;
}

div.tfw-factory-dialog>div>footer {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

div.tfw-factory-dialog>div>header {
    display: flex;
