  opacity                   : .1;
  transform                 : scale(1);
  transition                : all .5s ease;
  transition-timing-function: cubic-bezier(0.7, 1, .5, -.5);
}
div.tfw-behavior-touchable > div.show {
  transform : scale(1);
  transition: all .2s ease;
}

