div.tfw-view-Slider {
    position: relative;
    height: 48px;
    width: 100%;
}

div.tfw-view-Slider>label {
    position: absolute;
    display: block;
    left: 0;
    right: 0;
    font-size: 80%;
    transition: opacity .3s;
}

div.tfw-view-Slider>label.top {
    top: 0;
    text-align: left;
}
div.tfw-view-Slider.left>label.top {
    opacity: .4;
}

div.tfw-view-Slider>label.bottom {
    bottom: 2px;
    text-align: right;
}
div.tfw-view-Slider.right>label.bottom {
    opacity: .4;
}

div.tfw-view-Slider > div {
    position: absolute;
    display: block;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    margin: 18px 16px 22px 16px;
    height: 8px;
    overflow: visible;
    border-radius: 4px;
    cursor: pointer;
}
div.tfw-view-Slider.disabled {
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
    pointer-events: none;
    cursor: no-drop;
    opacity: .5;
}
div.tfw-view-Slider > div > div {
    top: 4px;
    left: 0%;
    position: absolute;
    display: block;
    width: 32px;
    height: 32px;
    line-height: 32px;
    text-align: center;
    border-radius: 100%;
    margin: -16px;
    cursor: pointer;
    font-size: 12px;
    overflow: hidden;
    transition: none;
}

div.tfw-view-Touchable.enabled {
  cursor: pointer;
}

div.tfw-view-ColorPicker {
    display: block;
}

div.tfw-view-ColorPicker > div.flex {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: stretch;
    margin: .25rem 0;
